img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0; }

.container {
  width: 100%;
  margin: 0 auto; }
  @media (max-width: 767px) {
